import React, { useEffect, useRef, useState } from "react"
import Countdown from "react-countdown"

import ImageGallery from "react-image-gallery"
import gsap from "gsap"
import "react-image-gallery/styles/css/image-gallery.css"
import "./SuperDeals.css"

function SuperDeals() {
  const products = [
    {
      title: "Dragon Touch Max10 Tablet Android 10.0 OS",
      currentPrice: "$129.99",
      originalPrice: "$349.99",
      rating: 5,
      reviews: 130,
      sizes: ["32GB", "64GB", "128GB"],
      original: "https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/home4/deal/product.png",
      thumbnail: "https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/home4/deal/1.png",
    },
    {
      title: "Lenovo Yoga Smart Tab with Google Assistant",
      currentPrice: "$199.99",
      originalPrice: "$299.99",
      rating: 4,
      reviews: 90,
      sizes: ["64GB", "128GB"],
      original: "https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/home4/deal/product.png",
      thumbnail: "https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/home4/deal/2.png",
    },
    {
      title: "Samsung Galaxy Tab A7 10.4",
      currentPrice: "$159.99",
      originalPrice: "$259.99",
      rating: 4.5,
      reviews: 110,
      sizes: ["32GB", "64GB"],
      original: "https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/home4/deal/product.png",
      thumbnail: "https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/home4/deal/3.png",
    },
    {
      title: "Fire HD 10 Tablet, 10.1\" 1080p Full HD",
      currentPrice: "$109.99",
      originalPrice: "$179.99",
      rating: 4,
      reviews: 75,
      sizes: ["32GB", "64GB", "128GB"],
      original: "https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/home4/deal/product.png",
      thumbnail: "https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/home4/deal/4.png",
    },
  ]

  const galleryItems = products.map((p) => ({
    original: p.original,
    thumbnail: p.thumbnail,
  }))

  const [selectedIndex, setSelectedIndex] = useState(0)
  const selectedProduct = products[selectedIndex]
  const detailsRef = useRef(null)

  useEffect(() => {
    if (detailsRef.current) {
      gsap.fromTo(
        detailsRef.current,
        { x: 100, opacity: 0 },
        { x: 0, opacity: 1, duration: 0.5, ease: "power2.out" }
      )
    }
  }, [selectedIndex])
const renderer = ({ days, hours, minutes, seconds, completed }) => {
  if (completed) {
    return <span className="countdown-ended">Deal Expired</span>
  } else {
    return (
    
      <div className="day-badges">
              <span className="day-badge active">{days}D</span>
              <span className="day-badge">{hours}H</span>
              <span className="day-badge"> {minutes}M</span>
              <span className="day-badge">{seconds}S</span>
              </div>
    )
  }
}
  return (
    <section className="super-deals">
      <div className="container">
        <div className="section-header">
          <div className="title-with-badges">
            <h2 className="section-title">Super Deals Of The Week</h2>
           
              <Countdown date={Date.now() + 2 * 24 * 60 * 60 * 1000} renderer={renderer} />
           
          </div>
          <a href="#" className="view-all">
            All Products →
          </a>
        </div>

        <div className="deals-content">
          <div className="main-product">
            <ImageGallery
              items={galleryItems}
              showPlayButton={false}
              showFullscreenButton={false}
              showNav={false}
              showBullets={false}
              showThumbnails={true}
              thumbnailPosition="left"
              onSlide={(index) => setSelectedIndex(index)}
            />
              <div className="product-details-superdeal" ref={detailsRef}>
            <div className="rating-superdeal">
              <div className="stars-superdeal">{"★".repeat(Math.floor(selectedProduct.rating))}</div>
              <span className="review-count-superdeal">({selectedProduct.reviews} Reviews)</span>
            </div>

            <h3 className="product-title-superdeal">{selectedProduct.title}</h3>

            <div className="pricing-superdeal">
              <span className="current-price-superdeal">{selectedProduct.currentPrice}</span>
              <span className="original-price-superdeal">{selectedProduct.originalPrice}</span>
            </div>

            <div className="product-actions-superdeal">
              <select className="size-select-superdeal">
                <option>Select Option</option>
                {selectedProduct.sizes.map((size, i) => (
                  <option key={i}>{size}</option>
                ))}
              </select>
              <button className="add-to-cart-superdeal">Add to Cart</button>
              <button className="wishlist-superdeal">♡</button>
            </div>
          </div>
          </div>

        

          <div className="featured-product-superdeal">
            <div className="featured-content-superdeal">
              <span className="featured-label-superdeal">Electronics</span>
              <h4>Samsung Ultra Wide 32" Monitor</h4>
              <div className="featured-pricing-superdeal">
                <span className="featured-price-superdeal">$230.00</span>
                <span className="featured-original-superdeal">$460.00</span>
              </div>
            </div>
            <img src="https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/home4/deal/side-product.png" alt="Monitor" />
            <button className="quick-add-superdeal">+</button>
          </div>
        </div>
      </div>
    </section>
  )
}

export default SuperDeals
